/* eslint import/no-anonymous-default-export: [2, {"allowArrowFunction": true}] */
const graph = {
  makePercentage(arr, options = { xAxis: 'x', yAxis: 'y', yType: 'number' }) {
    const total = arr.reduce((acc, cur) => acc + cur.total.value, 0);
    return arr.map(hits => {
      // console.log(hits.total.value);
      return {
        [options.xAxis]: hits.keyword || hits.legalStatus,
        [options.yAxis]: Array.isArray(options.yType)
          ? [Math.round((hits.total.value / total) * 10000) / 100]
          : Math.round((hits.total.value / total) * 10000) / 100,
      };
    });
  },
};

export default graph;
