import React, { useState, useEffect, useRef } from 'react';
import Chart from 'react-apexcharts';
import ApexCharts from 'apexcharts';

import { makeStyles } from '@material-ui/core';
import { searchKeyword } from '../Library/axios';
import graph from '../Library/graph';
import { UseLoading } from '../Custom/UseLoading';

const TreeMap = ({ year, setPage, keywords, contents, setContents }) => {
  const classes = useStyles();
  const ref = useRef(null);
  const { loading, setLoading, error, setError } = UseLoading();
  const [curYear, setCurYear] = useState(2020);
  const [series, setSeries] = useState([]);
  const [options] = useState({
    legend: {
      show: false,
    },
    chart: {
      id: 'treemap-chart',
      type: 'treemap',
      animations: {
        enabled: false,
        // animateGradually: {
        //   enabled: false,
        //   delay: 0,
        // },
      },
      events: {
        dataPointSelection: function (_, chartContext, config) {
          const { seriesIndex, dataPointIndex, w } = config;

          ref.current.style.display = 'none';
          setPage(1);
          setContents(w.config.series[seriesIndex].data[dataPointIndex].x);
        },
      },
      toolbar: {
        show: false,
      },
    },
    dataLabels: {
      enabled: true,
      offsetY: -2,
      style: {
        fontSize: '12px',
        fontWeight: 'bold',
        fontFamily: '"S-CoreDream-4Regular", "Noto Sans KR", "Roboto", "Helvetica", "Arial", sans-serif',
        colors: ['black'],
      },
      formatter: function (text, op) {
        if (op.value <= 1.2) return [`${text} ${op.value}%`];
        return [text, `${op.value}%`];
      },
    },
    colors: ['#51A7AD', '#F7A7BF', '#FFD53D', '#75E6DA', '#FFA384', '#B4AAA9', '#189AB4', '#116530'],
    plotOptions: {
      treemap: {
        distributed: true,
        enableShades: false,
      },
    },
    tooltip: {
      y: {
        formatter: function (val) {
          return val + '%';
        },
      },
    },
  });

  let cancelToken = () => {};
  useEffect(() => {
    const fallback = () => {
      const treeMap = ref.current?.firstChild;
      if (loading) {
        treeMap.style.display = 'none';
        ref.current.setAttribute('class', ref.current.className + ' fallback-box');
      }
    };

    const fetchData = async () => {
      setLoading(true);
      setError(false);
      fallback();
      const res = await searchKeyword(year, keywords, {}, () => setError(true), cancelToken);
      setSeries([{ data: graph.makePercentage(res) }]);
      ApexCharts.exec('treemap-chart', 'updateSeries', series);
      setLoading(false);
      setCurYear(year);
      ref.current.setAttribute('class', classes.root);
      ref.current.firstChild.style.display = 'block';
    };
    if (curYear !== year) fetchData();

    return () => cancelToken();
  }, [year, curYear, loading, setLoading, setError]);

  useEffect(() => {
    setSeries([]);
  }, []);

  return (
    <>
      <div className={classes.root} ref={ref}>
        <div>
          <Chart type='treemap' options={options} series={series} width={'100%'} height={600} />
        </div>
        {error && <div>    .  !</div>}
      </div>
    </>
  );
};

const useStyles = makeStyles(() => ({
  root: {
    width: '100%',
    height: 600,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    cursor: 'pointer',
    '& > div': {
      width: '100%',
      padding: '0 16px',
    },
  },
}));

export default TreeMap;
