import React, { useState, memo, useMemo, useEffect, useCallback } from 'react';
import Chart from 'react-apexcharts';

import { CircularProgress, Grid } from '@material-ui/core';
import { searchImages, searchLegalStatus } from '../Library/axios';
import graph from '../Library/graph';
import { MakeDestination } from '../Custom/MakeDesination';

const StackedBar = memo(({ year, contents, legalStatus, setLegalStatus, setImages, setTotal, loading, setLoading, error, setError }) => {
  const [series, setSeries] = useState([]);
  const [curYear, setCurYear] = useState(2020);
  const [curContents, setCurContents] = useState('');
  const { transformer } = MakeDestination();

  const legalStatusArr = useMemo(() => ['', '', '', '', ''], []);
  let cancelToken = () => {};

  const onSelectData = async legalStatus => {
    setError(false);
    setLoading(true);
    setImages([]);

    const res = await searchImages(curYear, curContents, legalStatusArr, legalStatus, { size: 24 }, () => setError(true), cancelToken);
    // console.log(res);
    setImages(
      res.hits.map(v => ({
        id: v['_id'],
        img: transformer(Array.isArray(v['_source']['']) ? v['_source'][''][0] : v['_source']['']),
      })),
    );
    setTotal(res.total.value);
    setLoading(false);
  };

  const stackedBarOptions = {
    options: {
      chart: {
        id: 'stackedbar-chart',
        type: 'bar',
        stacked: true,
        toolbar: { show: false },
        events: {
          dataPointSelection: async function (e, chartContext, { seriesIndex, dataPointIndex, w }) {
            const legalStatus = w.config.series[seriesIndex].name;
            setLegalStatus(legalStatus);
            onSelectData(legalStatus);
          },
          legendClick: async function (chartContext, seriesIndex, { config }) {
            const legalStatus = config.series[seriesIndex].name;
            setLegalStatus(legalStatus);
            onSelectData(legalStatus);
          },
        },
      },
      dataLabels: {
        enabled: true,
        textAnchor: 'middle',
        distributed: true,
        style: {
          // fontSize: '0.8rem',
          fontWeight: 'bold',
          fontFamily: '"S-CoreDream-4Regular", "Noto Sans KR", "Roboto", "Helvetica", "Arial", sans-serif',
          // colors: ['black'],
        },
        formatter: function (val, op) {
          return `${op.w.config.series[op.seriesIndex].name} ${val}%`;
        },
      },
      plotOptions: {
        bar: {
          // distributed: true,
          columnWidth: '100%',
          barHeight: '100%',
          horizontal: false,
          rangeBarGroupRows: true,
          dataLabels: { hideOverflowingLabels: true, position: 'center' },
        },
      },
      states: {
        active: {
          filter: {
            type: 'none',
          },
        },
      },
      fill: { opacity: 1 },
      xaxis: {
        tickPlacement: 'on',
        max: 1,
        // tickAmount: 2,
        labels: { show: false },
        axisBorder: { show: false },
        axisTicks: { show: false },
      },
      yaxis: { max: 100, labels: { show: false }, axisBorder: { show: false } },
      tooltip: {
        x: { show: false },
        y: {
          formatter: function (val) {
            return val + '%';
          },
        },
      },
      // eslint-disable-next-line no-dupe-keys
      legend: {
        show: true,
        horizontalAlign: 'center',
        position: 'bottom',
        onItemClick: {
          toggleDataSeries: false,
        },
      },
      grid: {
        show: false,
      },
    },
  };

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      setError(false);
      setImages([]);

      const res = await searchLegalStatus(year, contents, legalStatusArr, { size: 24 }, () => setError(true));
      setSeries(graph.makePercentage(res, { xAxis: 'name', yAxis: 'data', yType: [] }));
      const images = res
        .filter(v => v.legalStatus === '')[0]
        .hits.map(v => ({
          id: v['_id'],
          img: transformer(Array.isArray(v['_source']['']) ? v['_source'][''][0] : v['_source']['']),
        }));
      setImages(images);
      setTotal(res.filter(v => v.legalStatus === '')[0].total.value);
      setLoading(false);
    };

    if (curYear !== year || curContents !== contents) {
      fetchData();
      setCurYear(year);
      setCurContents(contents);
    }
    return () => cancelToken();
  }, [year, contents, series, curYear, curContents, setImages, legalStatusArr, transformer, setError, setLoading, setTotal]);

  return (
    <Grid container direction='column' style={{ textAlign: 'center' }}>
      {loading && (
        <>
          <CircularProgress style={{ alignSelf: 'center', marginBottom: 8 }} />
            
        </>
      )}
      {error && <div>    .</div>}
      <Chart series={series} options={stackedBarOptions.options} type='bar' width={'100%'} height={600} />
    </Grid>
  );
});

export default StackedBar;
