import React, { useState, memo, useContext } from 'react';
import { Grid, Typography, IconButton } from '@material-ui/core';

import { UseForm } from '../../Custom/UseForm';
import Controller from '../../Components/Controllers/Controller';

import TreeMap from '../../Components/TreeMap';
import StackedBar from '../../Components/StackedBar';

import clsx from 'clsx';
import globalUseStyles from '../../globalStyles';
import useStyles from './Styles';

import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Gallery from '../../Components/Gallery';

import PageContext from '../../Custom/PageContext';
import { UseLoading } from '../../Custom/UseLoading';

const Main = memo(() => {
  const gClasses = globalUseStyles();
  const classes = useStyles();

  // init values
  const initialValues = {};

  // validation
  const validate = (fieldValues = values) => {
    let temp = { ...errors };

    setErrors({ ...temp });
    if (fieldValues === values) return Object.values(temp).every(x => x === '');
  };

  // eslint-disable-next-line no-unused-vars
  const { values, errors, setErrors, handleInputChange } = UseForm(initialValues, true, validate);

  const { page, setPage } = useContext(PageContext);
  // console.log(' - page', page);

  const handleBack = () => setPage(page - 1);

  const [year, setYear] = useState('2020');
  const [contents, setContents] = useState('');
  const [legalStatus, setLegalStatus] = useState('');
  const [images, setImages] = useState([]);
  const [total, setTotal] = useState(0);
  const { loading, setLoading, error, setError } = UseLoading();
  const item = ['', '', '', '', '', '', '', '', '', '', ''];
  const renderItem = item.map((item, index) => (
    <Controller.Button
      key={index}
      disableFocusRipple={true}
      variant='text'
      color={item === contents ? 'primary' : 'inherit'}
      text={item}
      style={{ margin: '0px', fontWeight: '600' }}
      onClick={() => {
        setContents(item);
        setLegalStatus('');
      }}
    />
  ));

  return (
    <>
      <Grid item className={clsx(gClasses.root, classes.root)}>
        <Grid container className={clsx(classes.header, gClasses.mobileFlexColumnCenter)} spacing={2}>
          <Grid item sm={12} xs={12} className={clsx(gClasses.flexColumnCenter, gClasses.w100)}>
            <Grid item className={clsx(gClasses.flexRowCenter)} style={{ gap: 10 }}>
              <Controller.Button
                disableFocusRipple={true}
                variant='outlined'
                color={year === '2020' ? 'primary' : 'inherit'}
                text='2020'
                onClick={() => setYear('2020')}
                style={{ margin: '0px', fontWeight: '700' }}
              />
              <Controller.Button
                disableFocusRipple={true}
                variant='outlined'
                color={year === '2021' ? 'primary' : 'inherit'}
                text='2021'
                onClick={() => setYear('2021')}
                style={{ margin: '0px', fontWeight: '700' }}
              />
            </Grid>

            {year && (
              <Grid container className={gClasses.flexRowCenter}>
                <Grid item className={clsx(classes.subNav)} style={{ flexWrap: 'wrap' }} onClick={page === 0 ? () => setPage(1) : () => {}}>
                  {renderItem}
                </Grid>
              </Grid>
            )}
          </Grid>
        </Grid>
        <Grid container>
          {page === 0 && (
            <>
              <TreeMap setPage={setPage} year={year} keywords={item} contents={contents} setContents={setContents} />
              <Typography variant='subtitle2' align='center' style={{ width: '100%' }}>
                <p>    </p>
              </Typography>
            </>
          )}
          {page === 1 && (
            <>
              <Grid item md={4} xs={12} className={clsx(gClasses.flexRowStart)} style={{ padding: '0px 4%' }}>
                <IconButton color='inherit' onClick={handleBack}>
                  <ArrowBackIcon />
                </IconButton>
                {/* <Grid container direction='column' style={{ minWidth: 'max-content', margin: '0px 28px' }}> */}
                {/* <Typography color='inherit' variant='h6' style={{ fontWeight: '600' }}>
                  {year}
                </Typography> */}
                <Typography color='inherit' variant='h6' style={{ fontWeight: '600', minWidth: 'max-content' }}>
                  {`[ ${year} - ${contents}(${legalStatus}) ]`}
                </Typography>
                {/* </Grid> */}
              </Grid>
              <Grid container style={{}}>
                <Grid item md={4} sm={12} xs={12} style={{ cursor: 'pointer', width: '80%' }}>
                  <StackedBar
                    year={year}
                    contents={contents}
                    legalStatus={legalStatus}
                    setLegalStatus={setLegalStatus}
                    setImages={setImages}
                    setTotal={setTotal}
                    loading={loading}
                    setLoading={setLoading}
                    error={error}
                    setError={setError}
                  />
                </Grid>
                <Grid item md={8} xs={12}>
                  <Gallery
                    images={images}
                    setImages={setImages}
                    legalStatus={legalStatus}
                    year={year}
                    contents={contents}
                    total={total}
                    graphLoading={loading}
                  />
                </Grid>
              </Grid>
            </>
          )}
        </Grid>
        <Grid item xs={12} className={clsx(gClasses.flexRowCenter, gClasses.w100)} style={{ padding: '20px 0' }}>
          <Typography color='inherit' variant='subtitle1'>
            Copyright  branvip. All rights reserved.
          </Typography>
        </Grid>
      </Grid>
    </>
  );
});

export default Main;
