import axios from 'axios';

export const client = axios.create({
  headers: {
    'Content-Type': `application/json`,
  },
});

export const makeQuery = (query = [], options = {}) =>
  JSON.stringify({
    query: {
      bool: {
        must: [...query],
      },
    },
    size: 0,
    ...options,
  });

const makeQueryString = (legalStatusArr, legalStatus) => {
  const qs =
    legalStatus === ''
      ? {
          query_string: {
            ...queryString,
            query: `NOT(${legalStatusArr
              .filter(v => v !== '')
              .map((v, i) => (i < legalStatusArr.length - 2 ? `${v} OR ${v}* OR` : `${v} OR ${v}*`))
              .join(' ')})`,
          },
        }
      : { query_string: { ...queryString, query: `${legalStatus}* OR ${legalStatus}` } };
  return qs;
};

const regex = /["*","*","*","*"]/gm;
const queryString = {
  default_field: '',
  query: '',
};

/* Mood Tag     API */
export const searchKeyword = (
  year,
  keywords,
  options = {},
  cb = res => {
    console.log(res);
  },
  cancelToken,
) => {
  return Promise.all(
    keywords
      .map(v => (v === '' ? v : v.replace(regex, '')))
      .map((key, i) =>
        client
          .post(
            `/kipris_${year}/_search`,
            makeQuery([
              {
                exists: {
                  field: `Mood.${key}`,
                },
              },
            ]),
            { cancelToken: new axios.CancelToken(c => (c = cancelToken)) },
          )
          .then(res => ({ ...res.data.hits, keyword: keywords[i] }))
          .catch(err => {
            console.log(err);
            if (axios.isCancel(err)) return;
          }),
      ),
  );
};

export const searchLegalStatus = (year, keyword, legalStatusArr, options = {}, cb = () => {}, cancelToken) =>
  Promise.all(
    legalStatusArr
      .map(legalStatus => makeQueryString(legalStatusArr, legalStatus))
      .map((q, i) =>
        client
          .post(
            `/kipris_${year}/_search`,
            makeQuery(
              [
                {
                  exists: {
                    field: `Mood.${keyword === '' ? keyword : keyword.replace(regex, '')}`,
                  },
                },
                q,
              ],
              legalStatusArr[i] === '' ? { size: 24, from: 0 } : {},
            ),
            { cancelToken: new axios.CancelToken(c => (c = cancelToken)) },
          )
          .then(res => ({ ...res.data.hits, legalStatus: legalStatusArr[i] }))
          .catch(err => {
            console.log(err);
            if (axios.isCancel(err)) return;
            cb();
          }),
      ),
  );

export const searchImages = (year, keyword, legalStatusArr, legalStatus, options = {}, cb = () => {}, cancelToken) =>
  client
    .post(
      `/kipris_${year}/_search`,
      makeQuery(
        [
          {
            exists: {
              field: `Mood.${keyword === '' ? keyword : keyword.replace(regex, '')}`,
            },
          },
          makeQueryString(legalStatusArr, legalStatus),
        ],
        { size: 24, ...options },
      ),
      { cancelToken: new axios.CancelToken(c => (c = cancelToken)) },
    )
    .then(res => {
      // console.log(res);
      return { ...res.data.hits, year, keyword, legalStatus };
    })
    .catch(err => {
      console.log(err);
      if (axios.isCancel(err)) return;
      cb();
    });

export const searchImageDesc = (year, image_id, cb = () => {}, cancelToken) =>
  client
    .post(
      `/kipris_${year}/_search`,
      makeQuery(
        [
          {
            match: {
              _id: image_id,
            },
          },
        ],
        { size: 1 },
      ),
      { cancelToken: new axios.CancelToken(c => (c = cancelToken)) },
    )
    .then(res => ({ ...res.data.hits.hits[0]['_source'], image_id }))
    .catch(err => {
      console.log(err);
      if (axios.isCancel(err)) return;
      cb();
    });
