import React, { memo, useEffect, useRef, useState } from 'react';
import { makeStyles, Grid, Typography, Button, CircularProgress } from '@material-ui/core';
import PanoramaIcon from '@material-ui/icons/Panorama';
import { searchImageDesc, searchImages } from '../Library/axios';
import { UseModal } from '../Custom/UseModal';
import SergeInfoModal from '../Components/SergeInfoModal';
import { UseLoading } from '../Custom/UseLoading';
import { MakeDestination } from '../Custom/MakeDesination';

const Gallery = memo(({ year, contents, legalStatus, images, setImages, total, graphLoading }) => {
  const ref = useRef(null);
  const classes = useStyles();
  const [sergeInfo, setSergeInfo] = useState({});
  const [page, setPage] = useState(0);
  const [hasMore, setHasMore] = useState(true);

  const { loading, setLoading, error, setError } = UseLoading();
  const { open, handleOpen, handleClose } = UseModal();
  const { transformer } = MakeDestination();

  useEffect(() => {
    // let timer;
    if (ref.current) {
      if (graphLoading) {
        ref.current.lastChild.scrollTo({ top: 0 });
        ref.current.setAttribute('class', ref.current.className + ' fallback-box');
        ref.current.firstChild.style.display = 'none';
        ref.current.lastChild.style.display = 'none';
      } else {
        setPage(0);
        setHasMore(total > (page + 1) * 24);
        ref.current.setAttribute('class', classes.root);
        ref.current.firstChild.style.display = 'block';
        ref.current.lastChild.style.display = 'flex';
      }
    }
  }, [year, contents, legalStatus, classes.root, graphLoading]);

  let cancelToken = () => {};
  useEffect(() => {
    const getNextImages = async function () {
      setLoading(true);
      const res = await searchImages(
        year,
        contents,
        ['', '', '', '', ''],
        legalStatus,
        { size: 24, from: page * 24 },
        () => setError(true),
        cancelToken,
      );
      if (res.total.value === 0) {
        if (images.length < res.total.value) setPage();
        return;
      }
      setImages([
        ...new Set([
          ...(images || []),
          ...res.hits.map(v => ({
            id: v['_id'],
            img: transformer(Array.isArray(v['_source']['']) ? v['_source'][''][0] : v['_source']['']),
          })),
        ]),
      ]);
      setHasMore(total > page * 24);
      setLoading(false);
      // console.log(images, page, hasMore);
    };
    getNextImages();
    return () => cancelToken();
  }, [page]);

  const imageClickHandler = e => {
    searchImageDesc(year, e.target.dataset?.id).then(res => {
      handleOpen();
      setSergeInfo(res);
    });
  };

  return (
    <>
      <SergeInfoModal data={sergeInfo} open={open} handleClose={handleClose} />
      <Grid className={classes.root} container direction='column' ref={ref}>
        <Grid container>
          <Typography variant='subtitle2'>  : {total}</Typography>
        </Grid>
        <Grid
          container
          justifyContent={images?.length <= 0 ? 'center' : 'flex-start'}
          alignItems={images?.length <= 0 ? 'center' : 'flex-start'}
          spacing={2}
          className={classes.imgBox}
        >
          {images?.length > 0 ? (
            images.map((v, i) => (
              <Grid item xs={3} key={i} data-id={v.id} className={classes.img} onClick={imageClickHandler}>
                <img src={v.img} alt='logo' data-id={v.id} />
              </Grid>
            ))
          ) : (
            <Grid item xs={12}>
              <PanoramaIcon fontSize='large' />
              <Typography variant='body1'>  .</Typography>
            </Grid>
          )}
          {hasMore && (
            <Grid item xs={12}>
              {loading ? (
                <CircularProgress style={{ margin: '0 auto', display: 'block' }} />
              ) : (
                <Button variant='contained' onClick={() => setPage(prev => prev + 1)}>
                  
                </Button>
              )}
            </Grid>
          )}
        </Grid>
      </Grid>
    </>
  );
});

const useStyles = makeStyles(() => ({
  root: {
    height: 630,
    '& > div:first-child': {
      margin: '10px 0px',
    },
  },
  imgBox: {
    border: '2px solid rgb(0,0,0,0.15)',
    height: '90%',
    overflowY: 'scroll',
    textAlign: 'center',
  },
  img: {
    boxShadow: '1px 1px 5px rgb(0,0,0,0.2)',
    height: '50%',
    display: 'flex',
    alignItems: 'center',
    cursor: 'pointer',
    '&:hover': {
      opacity: 0.8,
    },
    '& img': {
      width: '100%',
      maxHeight: '100%',
    },
  },
}));

export default Gallery;
