import React, { useState, memo, useEffect, useContext } from 'react';
import { makeStyles, Grid, ListItemText, Drawer, List, ListItem } from '@material-ui/core';

import { UseForm } from '../Custom/UseForm';
import UseWidth from '../Custom/UseWidth';
import Controller from '../Components/Controllers/Controller';
import history from '../Config/History';

import clsx from 'clsx';
import globalUseStyles from '../globalStyles';

import MenuIcon from '@material-ui/icons/Menu';
import BranVip from '../Images/Icons/branvip.png';
import PageContext from '../Custom/PageContext';

const Navigation = memo(() => {
  const gClasses = globalUseStyles();
  const classes = useStyles();
  const { width } = UseWidth(window.innerWidth);
  const { setPage } = useContext(PageContext);
  // init values
  const initialValues = {};

  // validation
  const validate = (fieldValues = values) => {
    let temp = { ...errors };

    setErrors({ ...temp });
    if (fieldValues === values) return Object.values(temp).every(x => x === '');
  };

  const { values, errors, setErrors } = UseForm(initialValues, true, validate);

  // eslint-disable-next-line no-unused-vars
  const [logoSearchAnchorEl, setLogoSearchAnchorEl] = useState(null);
  // eslint-disable-next-line no-unused-vars
  const [serviceIntroAnchorEl, setServiceIntroAnchorEl] = useState(null);
  // eslint-disable-next-line no-unused-vars
  const [blogAnchorEl, setBlogAnchorEl] = useState(null);

  const handleClick = target => e => {
    if (target === 'logoSearch') {
      setLogoSearchAnchorEl(e.currentTarget);
    }
    if (target === 'serviceIntro') setServiceIntroAnchorEl(e.currentTarget);
    if (target === 'blog') setBlogAnchorEl(e.currentTarget);
  };

  // const handleClose = () => {
  //   setLogoSearchAnchorEl(null);
  //   setServiceIntroAnchorEl(null);
  //   setBlogAnchorEl(null);
  // };

  const logoSearch = () => {
    return (
      <Controller.Button
        disableFocusRipple={true}
        variant='text'
        color='inherit'
        text=' '
        onClick={handleClick('logoSearch')}
        style={{ margin: '0px', fontWeight: '600' }}
      />
    );
  };

  const serviceIntro = () => {
    return (
      <Controller.Button
        disableFocusRipple={true}
        variant='text'
        color='inherit'
        text=' '
        onClick={handleClick('serviceIntro')}
        style={{ margin: '0px', fontWeight: '600' }}
      />
    );
  };

  const blog = () => {
    return (
      <Controller.Button
        disableFocusRipple={true}
        variant='text'
        color='inherit'
        text=''
        onClick={handleClick('blog')}
        style={{ margin: '0px', fontWeight: '600' }}
      />
    );
  };

  const contact = () => {
    return (
      <Controller.Button
        disableFocusRipple={true}
        variant='text'
        color='inherit'
        text=''
        onClick={handleClick('contact')}
        style={{ margin: '0px', fontWeight: '600' }}
      />
    );
  };

  // Mobile Drawer
  const [mobileDrawer, setMobileDrawer] = useState({ right: false });

  const toggleDrawer = (anchor, open) => e => {
    if (e.type === 'keydown' && (e.key === 'Tab' || e.key === 'Shift')) return;
    setMobileDrawer({ ...mobileDrawer, [anchor]: open });
  };

  // Mobile Drawer List
  // const [activeTap, setActiveTap] = useState(null);

  // const handleNavTap = id => {
  //   if (activeTap !== id) {
  //     setActiveTap(id);
  //   } else if (activeTap === null) {
  //     setActiveTap(prevState => setActiveTap(prevState.id));
  //   }

  //   setMobileDrawer({ right: false });

  //   if (window.innerWidth <= 600) {
  //     if (id === 'logoSearch') {
  //     }

  //     if (id === 'Contact') history.push('/blog/contact');
  //   }

  //   if (id === 'logoSearch') {
  //   }
  // };

  const mobileDrawerList = () => {
    return (
      <Grid item xs={12} className={clsx(classes.list)} role='presentation'>
        <List classes={{ root: classes.listContainer }}>
          <ListItem button className={classes.listItemHover}>
            <Grid item style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', width: '100%' }}>
              <ListItemText primary=' ' />
            </Grid>
          </ListItem>
        </List>

        <List classes={{ root: classes.listContainer }}>
          <ListItem button className={classes.listItemHover}>
            <Grid item style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', width: '100%' }}>
              <ListItemText primary=' ' />
            </Grid>
          </ListItem>
        </List>

        <List classes={{ root: classes.listContainer }}>
          <ListItem button className={classes.listItemHover}>
            <Grid item style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', width: '100%' }}>
              <ListItemText primary='' />
            </Grid>
          </ListItem>
        </List>

        <List classes={{ root: classes.listContainer }}>
          <ListItem button className={classes.listItemHover}>
            <Grid item style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', width: '100%' }}>
              <ListItemText primary='' />
            </Grid>
          </ListItem>
        </List>
      </Grid>
    );
  };

  useEffect(() => {
    if (width >= 960) setMobileDrawer({ right: false });
  }, [width]);

  return (
    <>
      <Grid container>
        <Grid item className={clsx(gClasses.flexRowBtw, classes.root)}>
          <Grid item className={clsx(gClasses.flexRowBtw, gClasses.w100)}>
            <img src={BranVip} alt='BranVip' className={classes.logo} onClick={() => setPage(0)} style={{ cursor: 'pointer' }} />
            <Grid item className={clsx(gClasses.flexRowCenter, gClasses.tabletDown, gClasses.mobile)}>
              <Grid item style={{ margin: '0 16px' }}>
                {logoSearch()}
              </Grid>
              <Grid item style={{ margin: '0 16px' }}>
                {serviceIntro()}
              </Grid>
              <Grid item style={{ margin: '0 16px' }}>
                {blog()}
              </Grid>
              <Grid item style={{ margin: '0 16px' }}>
                {contact()}
              </Grid>
            </Grid>
          </Grid>

          {width <= 1280 && <MenuIcon fontSize='medium' className={classes.hamburger} onClick={toggleDrawer('right', true)} />}
        </Grid>
      </Grid>

      <Drawer anchor={'right'} open={mobileDrawer['right']} onClose={toggleDrawer('right', false)}>
        {mobileDrawerList()}
      </Drawer>
    </>
  );
});

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    height: '80px',
    padding: '16px 100px',
    zIndex: '200',
    boxShadow: '0px 6px 18px rgba(129, 139, 159, 0.22)',
    [theme.breakpoints.down('md')]: {
      padding: '24px !important',
    },
  },

  logo: {
    height: '60px',
    marginRight: '32px',
    cursor: 'pointer',
  },

  hamburger: {
    cursor: 'pointer',
  },

  list: {
    width: '300px',
  },

  accordionRoot: {
    '&.MuiAccordion-root:before': {
      backgroundColor: 'inherit',
    },
  },

  accordionContainer: {
    background: '#fff',
    color: '#000',
    boxShadow: 'none',

    '& .MuiAccordionSummary-root': {
      display: 'inherit',
      width: '100%',
      padding: '0px',
    },

    '& .MuiAccordionSummary-root.Mui-expanded': {
      display: 'inherit',
      width: '100%',
      minHeight: '0',
      padding: '0px',
    },

    '& .MuiAccordionSummary-content': {
      margin: '0px',
    },

    '& .MuiAccordionDetails-root': {
      padding: '0px',
    },
  },

  expanded: {
    margin: '0px !important',
  },

  listContainer: {
    width: '100%',
  },

  listItemHover: {
    '&:hover': {
      backgroundColor: '#4D89FF',
      color: '#fff',
      borderRadius: '0px',
      fontWeight: '600',
    },
  },

  listTitle: {
    paddingLeft: '5%',
  },
}));

export default Navigation;
